<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <div>
    <Headercomponer title="个人中心">
      <!-- 标题及操作区 -->
      <div slot="extra">

      </div>

      <!-- 主要内容 -->
      <div>
        <el-row :gutter="20">
          <el-col :span="8">
            <el-card class="item-card">
              <p>用户信息</p>
              <el-form>
                <el-form-item label="登录名">
                  <el-input v-model="userInfo.username" disabled></el-input>
                </el-form-item>

                <el-form-item label="用户名">
                  <el-input v-model="userInfo.nickname"></el-input>
                </el-form-item>

                <el-form-item label="电子邮箱">
                  <el-input v-model="userInfo.email"></el-input>
                </el-form-item>

                <el-form-item>
                  <el-button @click="updateUser" type="success">修改信息</el-button>
                </el-form-item>
              </el-form>
            </el-card>

            <el-card class="item-card">
              <p>重置密码</p>
              <el-form>
                <el-form-item label="旧密码">
                  <el-input v-model="pwdForm.old_pwd"></el-input>
                </el-form-item>

                <el-form-item label="新密码">
                  <el-input v-model="pwdForm.new_pwd"></el-input>
                </el-form-item>

                <el-form-item label="确认新密码">
                  <el-input v-model="pwdForm.re_pwd"></el-input>
                </el-form-item>

                <el-form-item>
                  <el-button @click="updatePassword" type="success">修改密码</el-button>
                  <el-button type="info">重置</el-button>
                </el-form-item>
              </el-form>
            </el-card>
          </el-col>
          <el-col :span="16">
            <el-card class="item-card">
              <p>修改头像</p>

              <el-upload
                ref="uploadRef"
                class="avatar-uploader"
                action=""
                :auto-upload="false"
                :on-change="onSelectFile"
                :show-file-list="false">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>

              <div class="item-button">
                <el-button @click="$refs.uploadRef.$el.querySelector('input').click()" type="primary"  icon="el-icon-edit-outline" >选择头像</el-button>
                <el-button @click="updateAvatar" :disabled="isDisabled" type="success" icon="el-icon-upload2">上传头像</el-button>
              </div>
            </el-card>
          </el-col>
        </el-row>
      </div>
    </Headercomponer>
  </div>
</template>

<script>
import { removeUserToken } from '@/utile/stores'
import { getUserInfo, uploadAvatar, updateUserInfo, updateUserPswd } from '@/api/userinfo'
import Headercomponer from '@/components/headercomponer.vue'
import { Message } from 'element-ui'
export default {
  data () {
    return {
      isDisabled: true,
      imageUrl: '',
      userInfo: {},
      pwdForm: {
        old_pwd: '',
        new_pwd: '',
        re_pwd: ''
      }
    }
  },
  methods: {
    // 图片上传时
    onSelectFile (file) {
      const reader = new FileReader()
      reader.readAsDataURL(file.raw)
      reader.onload = () => {
        this.imageUrl = reader.result
        this.isDisabled = false
      }
    },
    // 上传头像
    async updateAvatar () {
      await uploadAvatar(this.imageUrl)
      Message.success('上传头像成功')
    },

    // 更新用户信息
    async updateUser () {
      await updateUserInfo(this.userInfo)
      Message.success('更新成功')
    },

    // 修改密码
    async updatePassword () {
      const res = await updateUserPswd(this.pwdForm)
      Message.success('修改成功')
      removeUserToken()
      this.$router.replace('/login')
    }
  },
  async created () {
    this.$store.commit('tag/selecMenu', {
      path: this.$route.path,
      label: '个人中心'
    })

    const { data } = await getUserInfo()
    this.userInfo = data
    this.imageUrl = data.user_pic
  },
  components: {
    Headercomponer
  }
}
</script>

<style lang="less" scoped>
.item-card{
  margin-bottom: 20px;
  p{
    font-size: 20px;
    padding-bottom: 10px;
    font-weight: bold;
    color: #585858;
  }
}

.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 250px;
    height: 250px;
    line-height: 250px;
    text-align: center;
    border: 1px #9999 dashed;
    margin: 10px;
  }
  .avatar {
    width: 250px;
    height: 250px;
    display: block;
  }

  .item-button{
    margin-left: 15px;
  }
</style>
